@import url(https://fonts.googleapis.com/css2?family=New+Rocker&display=swap);
body {
  margin: 0;
  padding: 0;
  background-image: linear-gradient(to left, rgb(110, 0, 0), rgb(250, 77, 77));
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

h1 {
  color: rgb(246, 255, 125) !important;
  font-family: "New Rocker", cursive;
  font-size: 5rem;
  margin-top: 30px 0 0 0;
  letter-spacing: 3px;
}

.App {
  text-align: center;
}

.card {
  background-color: rgb(246, 255, 125);
  border: 1px solid rgb(112, 112, 112);
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  padding: 50px;
  cursor: pointer;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  transition: transform 0.3s ease-out;
}

.card:hover {
  transform: scale(1.03);
}

h1 {
  color: rgb(15, 15, 15);
}

.card-list {
  width: 70vw;
  margin: 0 auto;
  padding-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.search-box {
  padding: 10px;
  margin: 30px;
  border-radius: 5px;
  border-style: none;
  background-color: rgb(246, 255, 125);
  font-size: 1rem;
  color: rgb(180, 39, 39);
}

.search-box:focus {
  outline: none !important;
}

::-webkit-input-placeholder {
  color: rgb(180, 39, 39);
}

:-ms-input-placeholder {
  color: rgb(180, 39, 39);
}

::placeholder {
  color: rgb(180, 39, 39);
}

.footer {
  background-color: rgb(32, 28, 28);
  padding: 20px 0;
}

.footer a {
  color: rgb(251, 253, 106);
  text-decoration: none;

  letter-spacing: 1px;
}

